
export class PagerService {
/**
 * @param {?} totalItems
 * @param {?=} currentPage
 * @param {?=} pageSize
 * @return {?}
 */
getPager(totalItems: number, currentPage: number = 1, pageSize: number = 10) {
        let /** @type {?} */ totalPages = Math.ceil(totalItems / pageSize);

        let /** @type {?} */ startPage: number, /** @type {?} */ endPage: number;
        if (totalPages <= 10) {
            startPage = 1;
            endPage = totalPages;
        } else {
            if (currentPage <= 6) {
                startPage = 1;
                endPage = 10;
            } else if (currentPage + 4 >= totalPages) {
                startPage = totalPages - 9;
                endPage = totalPages;
            } else {
                startPage = currentPage - 5;
                endPage = currentPage + 4;
            }
        }

        let /** @type {?} */ startIndex = (currentPage - 1) * pageSize;
        let /** @type {?} */ endIndex = Math.min(startIndex + pageSize - 1, totalItems - 1);

        let /** @type {?} */ pages = Array.from(Array(endPage + 1 - startPage), (_ , i) => startPage + i );

        return {
            totalItems: totalItems,
            currentPage: currentPage,
            pageSize: pageSize,
            totalPages: totalPages,
            startPage: startPage,
            endPage: endPage,
            startIndex: startIndex,
            endIndex: endIndex,
            pages: pages
        };
    }
}
